import React from 'react'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';





const IndividualSearchCom = ({data}) => {
  return (
    <Card style={{ width: '18rem',height:"280px",borderRadius:"25px" }} className='mx-3 my-3' >
    <Card.Img variant="top" src={data.image} style={{height:"280px",borderRadius:"25px"}}  >

        </Card.Img>
 <div style={{position:"absolute", color:"red", top:"10px",left:"20px",fontSize:"25px",background:"white",borderRadius:"12px",paddingLeft:"10px",paddingRight:"10px"}}>{data.city}</div>
 <div style={{position:"absolute", color:"black", top:"52px",left:"20px",fontSize:"25px",background:"white",borderRadius:"12px",paddingRight:"10px",paddingLeft:"10px"}}>{data.totalHotels} <span>Hotels</span></div>
 <div style={{position:"absolute", color:"green", top:"95px",left:"20px",fontSize:"25px",background:"white",borderRadius:"12px",paddingRight:"10px",paddingLeft:"10px"}}><span>Avg.</span> Rs. {data.avg}</div>
  </Card>
  )
}

export default IndividualSearchCom;
