import React from 'react'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Cities from '../apis/Cities';
import Countries from '../apis/Countries';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/esm/Col';





const MoreSearches = () => {
    console.log(Cities[0][0])
    return (
        <div className='my-4 ' style={{display:"flex",flexWrap:"wrap",justifyContent:"center",width:"98.9vw"}}>
            <div style={{ display: "flex",flexWrap:"wrap",width:"98.9vw",justifyContent:"center" }} className=' '>
                <Card className=' mx-4 my-3 ' style={{ height: "350px", borderRadius: "0",minWidth:"250px" ,border:"none"}}>
                    <Card.Text className='mx-3' style={{ fontSize: "22px", fontWeight: "bold" }} >More Popular searches</Card.Text>
                    <Card.Body style={{ background: "whitesmoke", borderRadius: "22px", display: "flex",flexDirection:"column",maxHeight:"300px",overflow:"scroll"}} className=''>
                        
  {
                            Cities.map((d,index)=>{
                                return(
                                    <>
                                     <Row className='col-md-12' style={{ display: "flex", height: "25px",flexWrap:"auto",width:"30rem" }}>
                            <Col className='col-md-6'  style={{fontWeight:"600"}}><a href='#' style={{textDecoration:"none",color:"black"}}>{d[0]}</a></Col>
                            <Col className='col-md-6' style={{fontWeight:"600"}}><a href='#' style={{textDecoration:"none",color:"black"}}>{d[1]}</a></Col>
                        </Row>
                                    
                                    </>
                                )


                               
                            })



                        }


                    </Card.Body>
                </Card>
                <Card className=' mx-4 my-3' style={{ height: "350px", borderRadius: "0", minWidth:"250px",border:"none" }}>
                    <Card.Text className='mx-2' style={{ fontSize: "22px", fontWeight: "bold" }}>payvang international sites</Card.Text>

                    <Card.Body style={{ background: "whitesmoke", borderRadius: "22px", display: "flex",flexDirection:"column",overflow:"scroll"}} className=''>
                        {
                            Countries.map((d,index)=>{
                                return(
                                    <>
                                     <Row className='col-md-12 ' style={{ display: "flex", height: "25px",width:"30rem" }}>
                            <Col className='col-md-6'><a href='#' style={{textDecoration:"none",color:"black"}}>payvang <span style={{fontWeight:"bold"}}>{d[0]}</span></a></Col>
                            <Col className='col-md-6'><a href='#' style={{textDecoration:"none",color:"black"}}>payvang <span style={{fontWeight:"bold"}}>{d[1]}</span></a></Col>
                        </Row>
                                    
                                    </>
                                )


                               
                            })



                        }
                        {/* <div className='col-md-12' style={{ display: "flex", height: "25px",flexWrap:"auto" }}>
                            <div className='col-md-6'>Hotels in Ahmedabad</div>
                            <div className='col-md-6'>Hotels in Bengaluru</div>
                        </div> */}
                

                    </Card.Body>
                </Card>

            </div>

        </div>
    )
}

export default MoreSearches;
