import React from 'react'
import SingleItem from './SingleItem'
import Container from 'react-bootstrap/esm/Container'
import Row from 'react-bootstrap/esm/Row'
import Col from 'react-bootstrap/esm/Col'
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/esm/Button'
import hotelsData from '../apis/hotelsData'

const ItemsContainer = ({data}) => {
// console.log(data[1].countries)
  const loadData=(t)=>{
    const elements=document.getElementsByClassName("loadData");
    console.log(elements.classList)

    
  }
  return (
    <Container fluid className='my-5' >
        <Row className='my-3' style={{display:"flex",justifyContent:"center"}} >
           <Col  className=' col-md-8 text-center ' >
           <h3 style={{fontWeight:"600"}} className=''>{data[0].heading}</h3>
           </Col>
          
        </Row>
        <Row className='my-3' style={{display:"flex",justifyContent:"center"}}>
           <Col  className=' col-md-8 text-center' >

{

data[1].countries.map((d)=>{

  return (
    <>
    <Button className={'btn btn-sm mx-2' } variant={d.color} style={{borderColor:"black"}}>{d.name}</Button>
    </>
  )
})


}





           </Col>
          
        </Row>
     <Row className='' style={{display:"flex",justifyContent:"center"}}>
<Col className=' col-md-8' style={{display:"flex",flexWrap:"wrap",justifyContent:"center"}}>
{
    hotelsData.map((data)=>{
        return (
            <>
            <SingleItem data={data}/>
            </>
        )
    })
}
</Col>


     </Row>
      
    </Container>
  )
}

export default ItemsContainer
