import React from 'react'
import Button from 'react-bootstrap/esm/Button'

const DealsComp = ({data}) => {
    console.log(data)
  return (
  <>
   <div style={{ display: "flex", height: "10rem" }} className='my-3'>
                        <div style={{ width: "60%", border: "2px solid #F9F6EE", marginRight: "19px", display: "flex", backgroundColor: "#F9F6EE", borderRadius: "18px" }}>
                            <img src={data.image} style={{ width: "40%", height: "100%", borderRadius: "10px" }}></img>
                            <div className='mx-2 my-3' style={{ fontWeight: "bold", fontSize: "17px" }}>
                                <div className='my-1 mx-3' style={{textAlign:"center"}}>{data.text}</div>
                                <div style={{ fontWeight: "normal", fontSize: '13px', textAlign: "center" }}>
                                        
                                    <span className='mx-1'>Hotel</span></div>

                                <div style={{ fontSize: "15px", fontWeight: "normal", textAlign: "center" }}>{data.distance}</div>
                                <div style={{ fontSize: "17px", textAlign: "center" }}><span style={{ fontWeight: "bold", fontSize: "16px" }}>{data.rating}</span> <span style={{ fontWeight: "normal", fontSize: "12px" }}>({data.reviews} reviews)</span></div>
                            </div>
                        </div>
                        <div style={{ width: "40%", border: "2px solid orange", borderRadius: "19px", height: "80%" }} className='my-3 mx-1'>
                            <div className='mx-2 my-2' style={{ fontWeight: "700" }}>{data.rText}</div>
                            <div className=' mx-1 my-0' style={{ color: "darkgreen", fontWeight: "bold" }}><span style={{ fontSize: "12px" }}><i class="fa-solid fa-check mx-1"></i>Free Cancellation</span> <span style={{ fontSize: "12px" }}><i class="fa-solid fa-check mx-1"></i>Pay at the property</span></div>
                            <div style={{ display: "flex" }}>
                                <div style={{ fontWeight: "bold", color: "red", fontSize: "19px" }} className='mx-3 my-2'>₹{data.price}</div>
                                <Button className='btn btn-sm my-2' style={{ height: "33px", backgroundColor: "green",border:"2px solid green" }}>View Deal</Button>

                            </div>
                        </div>


                    </div>
  
  
  
  
  </>
  )
}

export default DealsComp
