import React from 'react'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form'
import { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import Swal from 'sweetalert2'
import Image from 'react-bootstrap/esm/Image';
import ItemsContainer from './ItemsContainer';
import CountriesApi from '../apis/CountriesApi';
import PopularSearchCom from './PopularSearchCom';
import PopularSearches from '../apis/PopularSearches';
import MoreSearches from './MoreSearches';


import { Link, useNavigate } from 'react-router-dom';


 
const Home = () => {
  const [show, setShow] = useState(false);
  const [childrenArray,setChildrenArray]=useState([

  ]);
  const navigate = useNavigate();
 
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [adults,setAdults]=useState(1);
  const [children,setChildren]=useState(0);
  const[rooms,setRooms]=useState(1);
  const[roomsState,setRoomsState]=useState(rooms)
  const [total,setTotal]=useState(Number(adults)+Number(children))
  const [destination,setDestination]=useState("");
  const [checkin,setCheckIn]=useState("");
  const[checkout,setCheckOut]=useState("");
  const [animation1,setAnimation1]=useState("Destination");
  var today = new Date();
var dd = today.getDate();
var mm = today.getMonth() + 1;
var yyyy = today.getFullYear();

if (dd < 10) {
   dd = '0' + dd;
}

if (mm < 10) {
   mm = '0' + mm;
} 
today=`${yyyy}-${mm}-${dd}`
console.log(today)
const [minDate,setMinDate]=useState(today)

  const applyChanges=()=>{
    if(adults===""){
      Swal.fire({
        title: 'Error!',
        text: 'Must Enter a Valid Number of Adults !',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return
    }
    if(children===""){
      Swal.fire({
        title: 'Error!',
        text: 'Must Enter a Valid Number of Children !',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return
    }
    if(rooms===""){
      Swal.fire({
        title: 'Error!',
        text: 'Must Enter a Valid Number of Rooms !',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return
    }
    

    handleClose()
  
    setTotal(Number(adults)+Number(children))
    setRoomsState(rooms)
    Swal.fire({
      title: 'Success',
      text: 'Changes has been applied successfully..',
      icon: 'success',
      confirmButtonText: 'Ok'
    });


  }
  const resetF=()=>{
    setAdults(1);
    setChildren(0);
    setRooms(1);
    setTotal(1);
    setRooms(1);

  }
  const fetchBestDeal=()=>{


    if(destination===""){
      Swal.fire({
        title: 'Oops!',
        text: 'Please Enter an appropriate destination',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return;
    }
    if(checkin===""){
      Swal.fire({
        title: 'Oops!',
        text: 'Please Select Check in date',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return;

    }
    if(checkout===""){
      Swal.fire({
        title: 'Oops!',
        text: 'Please Select check out date',
        icon: 'error',
        confirmButtonText: 'Ok'
      });
      return;
    }
    const search_detail={
      destination,checkin,checkout,total,roomsState
    
    }
    localStorage.setItem("search_detail",JSON.stringify(search_detail));
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.onmouseenter = Swal.stopTimer;
        toast.onmouseleave = Swal.resumeTimer;
      }
    });
    Toast.fire({
      icon: "success",
      title: "Getting Best Deal for you"
    }).then(()=>{
      navigate("/deal")
    })


console.log(search_detail)


    
  }
const updateD=(e)=>{
  setMinDate(e.target.value);
  setCheckIn(e.target.value);

}

const incrementAdults=()=>{
  setAdults(adults+1);




}
const decrementAdults=()=>{
  setAdults(adults-1);

}


const incrementChildren=()=>{
  console.log(childrenArray)
  var myArr=childrenArray;
  childrenArray.push({
    id:children+1
  });
  setChildren(children+1);
  setChildrenArray(myArr);
  console.log(childrenArray)




}
const decrementRooms=()=>{
  setRooms(rooms-1);

}

const incrementRooms=()=>{
  setRooms(rooms+1);




}
const decrementChildren=()=>{
  setChildren(children-1);
  var myArr=childrenArray;
  myArr.pop();
  setChildrenArray(myArr);

}






  return (
    <>
    <div style={{width:"98.8vw",display:"flex",justifyContent:"center"}} className='my-5'  >
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Select Rooms and Guests below</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <Form>
      <Form.Group className="mb-3" controlId="formBasicNumber" style={{display:"flex",fontWeight:"700"}}>
        <Form.Label className='col-md-6 mx-2 my-1'>Adults</Form.Label>
        <Form.Group style={{display:"flex"}}>
          <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} onClick={decrementAdults} disabled={adults<=1}> - </Button>
        <Form.Control className="mx-3" type="number"  value={adults} onChange={(e)=>{setAdults(e.target.value)}} style={{width:"45px",textAlign:"center",MozAppearance:"textfield"}} min={1} />
        <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} disabled={adults>=48} onClick={incrementAdults}> + </Button>
        </Form.Group>
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicNumber" style={{display:"flex",fontWeight:"700"}}>
        <Form.Label className='col-md-6 mx-2 my-1'>Children</Form.Label>
        <Form.Group style={{display:"flex"}}>
          <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} onClick={decrementChildren} disabled={children<=0}> - </Button>
        <Form.Control className="mx-3" type="number"  value={children} onChange={(e)=>{setChildren(e.target.value)}} style={{width:"45px",textAlign:"center",MozAppearance:"textfield"}} min={1}/>
        <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} onClick={incrementChildren} disabled={children>41}> + </Button>
        </Form.Group>
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicNumber" style={{display:"flex",fontWeight:"700"}}>
        <Form.Label className='col-md-6 mx-2 my-1'>Rooms</Form.Label>
        <Form.Group style={{display:"flex"}}>
          <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} onClick={decrementRooms} disabled={rooms<=1}> - </Button>
        <Form.Control className="mx-3" type="number" value={rooms} onChange={(e)=>{setRooms(e.target.value)}} style={{width:"45px",textAlign:"center",MozAppearance:"textfield"}} min={1}/>
        <Button style={{fontWeight:"bold",fontSize:"18px",borderRadius:"65%",height:"35px",width:"35px",margin:0,padding:0}} onClick={incrementRooms} disabled={rooms>=40}> + </Button>
        </Form.Group>
      </Form.Group>

{
  childrenArray.length>0?<div style={{maxHeight:"40vh",overflowY:"auto"}}>
  <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
   
     {
      childrenArray.map((d,index)=>{

        return(
          <>
          {
            index==0?<>
               <div className='mx-3'>
        <h6 style={{fontSize:"18px"}}>Children's ages (Required)</h6>
      </div>
            
            </>:<></>
          }
           <Form.Group style={{display:"flex"}} className='my-3'>
        <div className='col-md-8 mx-4 my-2' style={{fontWeight:"bold",fontSize:"14px"}}>Child {d.id}</div>
        <Form.Select aria-label="Default select example" style={{width:"70px"}}>
      <option value="1">1</option>
      <option value="2">2</option>
      <option value="3">3</option>
      <option value="4">4</option>
      <option value="5">5</option>
      <option value="6" selected>6</option>
      <option value="7">7</option>
      <option value="8">8</option>
      <option value="9">9</option>
      <option value="10">10</option>
      <option value="11">11</option>
      <option value="12">12</option>
      <option value="13">13</option>
      <option value="14">14</option>
      <option value="15">15</option>
      <option value="16">16</option>
      <option value="17">17</option>
    </Form.Select>
      </Form.Group>
          </>
        )
      })
     }
      
      </Form.Group> 
  
  
  
  </div>:<div></div>




}

    </Form>
    




   
        </Modal.Body>
        <Modal.Footer style={{display:"flex",justifyContent:"start"}}>
          <div className='col-md-9'>
        <Button variant="danger mx-2" onClick={resetF} style={{}}>
        Reset
      </Button>
      <Button variant="primary" onClick={applyChanges} className='mx-2'>
        Apply
      </Button>
      </div>
          <Button variant="warning" onClick={handleClose} style={{}}>
            Close
          </Button>
        
        </Modal.Footer>
      </Modal>

      <Row className='' style={{display:"flex",width:"99vw",justifyContent:"center"}}>
        <Col className='col-md-9' style={{}}>
        <Card>
      <Card.Body>
        <Card.Title className='my-3' style={{fontWeight:"bold",fontSize:"25px"}}>We Compare hotel prices from multiple sites</Card.Title>
        <Row style={{border:"1px solid black",borderRadius:"12px"}} className='mt-5'>
        <Col className='col-md-2 borderController' style={{height:"55px",width:"220px",display:"flex"}} >
            
            <div className='d-flex justify-content-center align-items-center'><i className='fa fa-search' style={{fontSize:"23px"}}></i></div>
         <div style={{}}>
            <div style={{fontSize:"12px",height:"22px"}} className='mx-2 mt-1' >{animation1}</div>
            <input type='text' placeholder='Where to ?' style={{border:"none",width:"179.9px",paddingLeft:"5px",outline:"none"}}className='mx-1' value={destination} onChange={(e)=>{setDestination(e.target.value)}}></input>
         </div>
            
        </Col>
            <Col className='col-md-2 borderController' style={{height:"55px",width:"220px",display:"flex"}} >
            <div className='d-flex justify-content-center align-items-center'><i className='fa-solid fa-folder' style={{fontSize:"23px"}}></i></div>
         <div style={{marginLeft:"4px"}}>
            <div style={{fontSize:"12px",height:"22px"}} className='mx-2 mt-1' >Check in</div>
            <input type="date" style={{border:"none",width:"179.9px",outline:"none",fontWeight:"bold",width:"150px"}}className='mx-1' min={minDate} onChange={(e)=>{updateD(e)}}></input>
         </div>
            </Col>
            <Col className='col-md-2 borderController'  style={{height:"55px",width:"220px",display:"flex"}} >
            <div className='d-flex justify-content-center align-items-center'><i className='fa-solid fa-money-check' style={{fontSize:"23px"}}></i></div>
         <div style={{marginLeft:"4px"}}>
            <div style={{fontSize:"12px",height:"22px"}} className='mx-2 mt-1' >Check out</div>
            <input type="date" style={{border:"none",width:"179.9px",outline:"none",fontWeight:"bold",width:"150px"}}className='mx-1' min={minDate} onChange={(e)=>{setCheckOut(e.target.value)}}></input>
         </div>
            </Col>
            <Col className='col-md-4' style={{height:"55px",display:"flex"}} >

            <div className='d-flex justify-content-center align-items-center'><i className='fa-solid fa-bed' style={{fontSize:"23px"}}></i></div>
         <div style={{marginLeft:"4px"}} onClick={handleShow}>
            <div style={{fontSize:"12px",height:"22px"}} className='mx-2 mt-1' >Guests and rooms</div>
            <label style={{border:"none",width:"179.9px",outline:"none",fontWeight:"bold",width:"150px",cursor:"pointer"}}className='mx-1' >{total} Guests , {roomsState} Room</label>
            
         </div>
         <Link style={{height:"40px",width:"100px",textDecoration:"none",color:"white",border:"2px solid #0096FF",background:"#0096FF",textAlign:"center",paddingTop:"5px",fontWeight:"bold",borderRadius:"15px",width:"160px"}} className='my-2 mx-2' onClick={fetchBestDeal}>Search</Link>



            </Col>
           
            
        </Row>
        <Row className='my-5'>
          <Col className='col-md-2 text-center' style={{}} ><Image src='Clients-Images/hotels.png' style={{height:"15px",width:"80px"}}/></Col>
          <Col className='col-md-2 text-center' style={{}} ><Image src='Clients-Images/booking.png' style={{height:"15px",width:"80px"}}/></Col>
          <Col className='col-md-2 text-center' style={{}} ><Image src='Clients-Images/makemy.png' style={{height:"15px",width:"80px"}}/></Col>
          <Col className='col-md-2 text-center' style={{}} ><Image src='Clients-Images/trip.png' style={{height:"15px",width:"80px"}}/></Col>
          <Col className='col-md-2 text-center' style={{}} ><Image src='Clients-Images/oyo.png' style={{height:"15px",width:"80px"}}/></Col>
          <Col className='col-md-2 text-center' style={{display:"flex",justifyContent:"center",alignItems:"center"}} ><h6 className='my-1' style={{fontSize:"12px"}}>+100s more</h6></Col>
        </Row>
    
      </Card.Body>
    </Card>
        
        
        
        
        
        </Col>
      </Row>

    
    
    </div>
    <ItemsContainer data={CountriesApi}/>
    <PopularSearchCom data={PopularSearches} />
    <MoreSearches/>
    </>

  )
}

export default Home
