import React from 'react'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';





const SingleItem = ({data}) => {
  return (
    <Card style={{ width: '18rem' }} className='mx-3 my-3'>
    <Card.Img variant="top" src={data.image} style={{height:"240px"}}/>
    <Card.Body>
      <Card.Title>{data.name}</Card.Title>
      {/* <Card.Text>
        Some quick example text to build on the card title and make up the
        bulk of the card's content.
      </Card.Text> */}
      <Card.Text>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Maiores officia eligendi est ab, quod asperiores. Vero molestias illum sunt. Esse.
      </Card.Text>
      <Button variant="primary" className='btn btn-sm'>Know More</Button>
    </Card.Body>
  </Card>
  )
}

export default SingleItem
